export let config = {
    selector: ".card"
}

export function init(cards_input){
    cards_input.forEach((card) => {
        let card_button = document.createElement("button")
        card_button.classList.add("button")

        card.append(card_button)

        card_button.addEventListener("click", (e) => {
            console.log(card)
        })
    })
}